import { Component } from '@angular/core';
import { IonicPage, AlertController, NavController, LoadingController, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
@IonicPage({
  name: 'page-withdraw-to-bank',
  segment: 'withdraw-to-bank'
})

@Component({
  selector: 'page-withdraw-to-bank',
  templateUrl: 'withdraw-to-bank.html'
})

export class WithdrawToBankPage {

  constructor(public navCtrl: NavController, public user: UserProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {


  }

  // go to summary witdraw page
  next() {
    this.navCtrl.push('page-withdraw-summary');

  }

  // process send button
  sendData() {
    let loader = this.loadingCtrl.create({
      content: "Please wait..."
    });
    // show message
    let toast = this.toastCtrl.create({
      showCloseButton: true,
      cssClass: 'profiles-bg',
      message: 'Your Data was Edited',
      duration: 3000,
      position: 'top'
    });

    loader.present();

    setTimeout(() => {
      loader.dismiss();
      toast.present();
      // back to home page
      this.navCtrl.setRoot('page-home');
    }, 3000)
  }

}



